import React from 'react';
import avator from "../../statics/picture/avator.jpg";
import Hospital from "../../statics/picture/gszyy.jpg";
import {
    CenterIntroWrapper,
    CenterIntroInfo,
    Title,
    CenterIntro,
    IntroWordInfo,
    IntroImgInfo,
    CenterDoctor,
    DoctorItem,
    DoctorImg,
    DoctorInfo
} from './style';

function HospitalIntro() {
    return (
        <CenterIntroWrapper>
            <CenterIntroInfo>
                <CenterIntro>
                    <Title>CENTER INTRODUCTION</Title>
                    <IntroWordInfo>
                        Lorem ipsum dolor sit amet, consectetur adipiscing elit. Aenean euismod bibendum laoreet. Proin gravida dolor sit amet lacus accumsan et viverra justo commodo. Proin sodales pulvinar tempor. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Nam fermentum, nulla luctus pharetra vulputate, felis tellus mollis orci, sed rhoncus sapien nunc eget.
                    </IntroWordInfo>
                    <IntroImgInfo>
                        <img alt="center's img" src={Hospital}/>
                    </IntroImgInfo>
                </CenterIntro>
                <CenterDoctor>
                    <Title>OUR DOCTORS</Title>
                    <DoctorItem>
                        <DoctorImg>
                            <img alt="Doctor's avator" src={avator}/>
                        </DoctorImg>
                        <DoctorInfo>
                            <h3 className="name"></h3>
                            <p className="job-title"></p>
                            <p className="introduction">555</p>
                        </DoctorInfo>
                    </DoctorItem>
                    <DoctorItem>
                        <DoctorImg>
                            <img alt="Doctor's avator" src={avator}/>
                        </DoctorImg>
                        <DoctorInfo>
                            <h3 className="name"></h3>
                            <p className="job-title"></p>
                            <p className="introduction">555</p>
                        </DoctorInfo>
                    </DoctorItem>
                </CenterDoctor>
            </CenterIntroInfo>
        </CenterIntroWrapper>
    );
}

export default HospitalIntro;