import Login from "../manage/login";
import Index from "../manage/index";

import React from 'react';
import { RenderRoutes } from '../router/utils';

import CarouselManage from "../manage/containers/carousel";
import StaffManage from "../manage/containers/staff";
import MomentManage from "../manage/containers/moment";
import ContentManage from "../manage/containers/content";
import LearnTCM from "../manage/containers/learn";
import ArticleManage from "../manage/containers/article";
import ArticleAdd from "../manage/containers/addArticle";
import ArticleUpdate from "../manage/containers/updateArticle";
import PediatricMassageManagement from "../manage/containers/pediatric";
import RoleManagement from "../manage/containers/role";

const Ui = ({routes}) => (<div>
    <h3>Ui
    </h3>
    <RenderRoutes routes={routes}/>
</div>);
const Button = () => <h3>Button</h3>;
const Icon = () => <h3>Icon</h3>;
export const menus = [    // 
    { path: '/manage/index/carousel', name: '', icon: 'file-image', component: CarouselManage },
    { path: '/manage/index/staff', name: '', icon: 'idcard', component: StaffManage },
    { path: '/manage/index/moment', name: '', icon: 'profile', component: MomentManage },
    { path: '/manage/index/learn', name: '', icon: 'read', component: LearnTCM, routes: [
            {path: '/manage/index/learn/menu', name: '', icon: 'menu', component: ContentManage },
            {path: '/manage/index/learn/articles', name: '', icon: 'book', component: ArticleManage }
        ]
    },{
      path: '/manage/index/pediatric', name: '', icon: 'smile', component: PediatricMassageManagement
    },{
        path: '/manage/index/role', name: '', icon: 'team', component: RoleManagement
    },{ path: '/manage/index/UI', name: 'UI', icon:'video-camera', component: Ui , routes: [
            {path: '/manage/index/UI/button', name: '', icon: 'video-camera', component: Button },
            {path: '/manage/index/UI/Icon', name: '', icon: 'video-camera', component: Icon }
        ]
    }
];

export const main = [
    { path: '/manage/login', exact: true, name: '', component: Login, meta: {
            isAuth: true
        }},
    { path: '/manage/article/add', exact: true, name: "", component: ArticleAdd},
    { path: '/manage/article/update/:id', exact: true, name: "", component: ArticleUpdate},
    { path: '/manage', exact: true,  name: '', Redirect: '/manage/index'},
    { path: '/manage/index', name: '', component: Index, routes: menus}
];



export const routerConfig =  {
    main, menus
};